import DeleteIcon from "@mui/icons-material/DeleteOutlined";
import EditIcon from "@mui/icons-material/EditOutlined";
import InfoOutlined from "@mui/icons-material/InfoOutlined";
import MoreVertOutlined from "@mui/icons-material/MoreVertOutlined";
import { Box, Card, IconButton, Skeleton, Typography } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { Outlet, useLocation, useNavigate, useParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import ResponsiveTabBar from "../../../../components/ResponsiveTabBar";
import { TabView } from "../../../../tabs";
import useDialogProps from "../../../../utils/useDialogProps";
import useFetch from "../../../../utils/useFetch";
import useNavigateTask from "../../../../utils/useNavigateTask";
import ProductDeleteDialog from "../../components/ProductDeleteDialog";
import ProductFormDialog from "../../components/ProductFormDialog";


const ProductDetails = () => {
    const { inject } = useAuth();

    const middleware = useMiddleware('Products.Main');

    const location = useLocation();
    const navigate = useNavigate();
    const params = useParams();
    const navigateTask = useNavigateTask();

    const { data, loading, reload } = useFetch({
        deps: [params.productId],
        handler: () => middleware.details(params.productId),
        initialState: {
            data: { product: {} },
            loading: true
        }
    });

    const { product } = data;

    const { enqueueSnackbar } = useSnackbar();

    const [formDialogProps, openFormDialog, closeFormDialog] = useDialogProps("edit", {
        hashDeps: ["defaultValues"]
    })
    const handleUpdate = () => {
        enqueueSnackbar('مشخصات محصول بروزرسانی گردید.');
        closeFormDialog();
        reload();
    }

    const taskManage = useTask('People.manage');
    const [deleteDialogProps, openDeleteDialog, closeDeleteDialog] = useDialogProps("delete", {
        hashDeps: ["product"]
    })
    const handleDelete = () => {
        enqueueSnackbar('محصول حذف گردید.');
        closeDeleteDialog();
        navigateTask(taskManage, { replace: true });
    }

    const injectedHooks = Object.assign({}, ...(inject('Products', 'ProductDetails.Hooks') || []));

    const task = useTask('Products.details')


    return (
        <TabView
            loading={loading}
            {...Object.assign(
                {},
                task(product),
                ...(inject('Products', 'ProductDetails.TabView', { loading, product }) || [])
            )}
        >
            <ResponsiveContainer fullHeight>
                <Card
                    variant="outlined"
                    sx={{
                        flexGrow: 1,
                        display: 'flex',
                        flexDirection: 'column'
                    }}
                >
                    <Box sx={{
                        display: "flex",
                        alignItems: 'start',
                        justifyContent: 'start',
                        p: { xs: 2, sm: 3 }
                    }}
                    >
                        <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
                            {inject('Products', 'ProductDetails.PreHeader', { product, loading, reload, params, ...injectedHooks })}

                            <Box sx={{ flexGrow: 1 }}>
                                <Typography variant="h5" sx={{ fontWeight: 600, mb: 1 }}>
                                    {loading ? <Skeleton variant="text" width="25%" /> : product.name}
                                </Typography>
                                {/* {loading ? <Skeleton variant="text" width="50%" /> : (
                                    <Stack direction="row" spacing={0.5} sx={{ display: "inline-flex" }}>
                                        {data.product?.roles && product.roles.map(
                                            (role, index) => <Chip key={index} label={role.name} size="small" />)
                                        }
                                    </Stack>
                                )} */}
                            </Box>
                        </Box>
                        {!loading && (
                            <ResponsiveActionBar
                                actions={sortBy([
                                    {
                                        title: 'ویرایش',
                                        icon: <EditIcon fontSize="inherit" />,
                                        onClick: () => openFormDialog({ defaultValues: product }),
                                        buttonProps: {
                                            variant: "contained"
                                        },
                                        priority: 100
                                    },
                                    {
                                        title: 'حذف',
                                        icon: <DeleteIcon fontSize="inherit" />,
                                        onClick: () => openDeleteDialog({ product }),
                                        buttonProps: {
                                            variant: "contained"
                                        },
                                        priority: 200
                                    },
                                    ...flatten((inject('Products', 'ProductDetails.ActionsList', { product, loading, reload, params, ...injectedHooks }) || []))
                                ], o => o.priority)}
                                ResponsiveActionComponent={IconButton}
                                ResponsiveActionIcon={<MoreVertOutlined />}
                            />
                        )}
                    </Box>
                    <ResponsiveTabBar
                        tabs={sortBy([
                            {
                                label: 'مشخصات محصول',
                                value: `/products/${params.productId}`,
                                icon: <InfoOutlined />,
                                priority: 100
                            },
                            ...(inject('Products', 'ProductDetails.TabsList', { product, loading, reload, params, ...injectedHooks }) || [])
                        ].map(o => ({
                            ...o, onClick: () => navigate(
                                o.value,
                                { state: { prev: location } }
                            )
                        })), o => o.priority)}
                        value={location.pathname}
                    >
                        <Outlet context={{ ...data, loading }} />
                    </ResponsiveTabBar>
                </Card>

                <ProductFormDialog
                    {...formDialogProps}
                    onCreateOrUpdate={handleUpdate}
                />

                <ProductDeleteDialog
                    {...deleteDialogProps}
                    onDelete={handleDelete}
                />

                {inject('Products', 'ProductDetails.Footer', { product, loading, reload, params, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}

export default ProductDetails